<template>
	<view class="side-box">
		
		<!-- 论坛内容列表 -->
		<view style="margin-bottom: 50rpx;">
			<view style="display: flex;align-items: center;">
				<image :src="articleData.avatar && articleData.avatar.split(',')[articleData.avatar.split(',').length-1]" mode="" style="width: 90rpx;height: 90rpx;border-radius: 100%;margin-right: 20rpx;" @click="toTalkPage(item.userName,item.createdBy)"></image>
				<view>
					<view style="color: #333;font-weight: 550;">{{articleData.userName}}</view>
					<view style="margin-top: 10rpx;font-size: 22rpx;color: #666;">
						<text>{{happenTimeFun(articleData.createdAt)}}</text>
					</view>
				</view>
				<view class="" style="margin-left: 350rpx;color: #000;" @click="saveItemClick">收藏</view>
			</view>
			
			<view style="color: #000;margin: 20rpx 0;">{{articleData.content}}</view>
			<view class="">
				<image :src="articleData.imgUrl" mode="aspectFill" style="width: 430rpx;height: 600rpx;border-radius: 5rpx;" v-if="articleData.imgUrl !== ''"></image>
				<view class="" v-else></view>
			</view>
			
			
			
			<view style="display: flex;align-items: center;margin-top: 36rpx;">
				
				
				<view style="display: flex;align-items: center;margin-right: 80rpx;">
					<image src="/static/icons/评论.png" mode="" style="width: 50rpx;height: 50rpx;margin-right: 15rpx;"></image>
					<text style="color: #666;font-weight: 550;">{{articleData.momentCommentList.length}}</text>
				</view>	
				
				
			</view>		
		</view>
		
		<!-- 评论 -->
		<view>
			
			<view style="display: flex;align-items: center;justify-content: space-between;">
				<view style="font-size: 28rpx;color: #222;font-weight: 550;">评论 {{articleData.momentCommentList.length}} 条</view>
				
				
				
			</view>
			<!-- 评论列表 -->
			<view style="display: flex;justify-content: space-between;margin: 40rpx 0;" v-for="(item,index) in articleData.momentCommentList">
				<image :src="item.avatar" mode="" style="width: 70rpx;height: 70rpx;border-radius: 100%;margin-right: 35rpx;"></image>
				<view style="margin-right: 15rpx;flex: 1;">
					<view style="font-size: 28rpx;color: darkorange;font-weight: 550;margin-bottom: 12rpx;">{{item.userName}}</view>
					<view style="max-width: 500rpx;margin-bottom: 12rpx;">{{item.comment}}</view>
					<view style="color: #666;">
						<text style="margin-right: 20rpx;font-size: 22rpx;">{{happenTimeFun(item.createdAt)}}</text>
						
					</view>
				</view>
				
				<view>
					<image src="/static/icons/点赞 (1).png" mode="" style="width: 40rpx;height: 40rpx;"></image>
					<view>20</view>
				</view>
				
				
			</view>
			
		</view>
		
		<!-- 底部 -->
		<view class="footer-buttons">
			<view>
				<image src="/static/icons/smile.png" mode="" style="width: 40rpx;height: 40rpx;"></image>
			</view>	
			
			<view class="" style="color: #666;" @click="openComment">
				请输入内容
			</view>
			
			<view class="" style="display: flex;align-items: center;">
				<image src="/static/icons/send-message.png" mode="" style="width: 50rpx;height: 50rpx;" @click="openComment"></image>
	
			</view>
		</view>
		
		
		<!-- 评论模态框 -->
		<view class="comment_box" v-if="comment_box_modal_show">
			<view style="padding: 20rpx;width: 100%;height:200rpx;">
				<textarea name="" id="" cols="30" rows="10" v-model="comment_Params.comment" placeholder="请输入评论"></textarea>
				
			</view>
			<view style="width: 180rpx;height: 60rpx;float: right;margin-top: 30rpx;">
				<button style="width: 100%;height: 100%;font-size: 20rpx;line-height: 60rpx;background-color: rgb(33, 118, 255);color: #fff;"  @click="sendMessage">发表</button>
			</view>
		</view>
		
		
	</view>
</template>

<script>
	export default {
		data() {
			return {
				articleId:'',
				articleData:{},
				comment_box_modal_show:false,
				comment_Params:{
					momentId:"",
					comment:"",
					status:0
				}
			}
		},
		onLoad(option) {
			this.articleId = option.id;
			this.comment_Params.momentId = option.id
			this.getArticleDetail(option.id)
		},
		methods: {
			toArticleDetail(){
				uni.navigateTo({
					
				})
			},
			getArticleDetail(ids){
				uni.request({
					url:this.$elyasApi+'article/detail?id='+ids,
					method:'POST',
					data:this.selectItems,
					header:{
						"content-type":"application/json",
						"accessToken":this.$store.state.userInfo.token
					},
					success:(data)=> {
						this.articleData = data.data.data;
					}
					
				});
			},
			happenTimeFun(num){//时间戳数据处理
						
			let date = new Date(num);
			
			//时间戳为10位需*1000，时间戳为13位的话不需乘1000
			
			        let y = date.getFullYear();
			
			        let MM = date.getMonth() + 1;
			
			        MM = MM < 10 ? ('0' + MM) : MM;//月补0
			
			        let d = date.getDate();
			
			        d = d < 10 ? ('0' + d) : d;//天补0
			
			      
			        return y + '-' + MM + '-' + d;
			
			},
			// 收藏
			saveItemClick(){
				uni.request({
					url:this.$elyasApi+'collect/addOrCancel',
					method:'POST',
					data:{momentId:this.articleId,status:1},
					header:{
						"content-type":"application/json",
						"accessToken":this.$store.state.userInfo.token
					},
					success:(data)=> {
						
					}
					
				});
			},
			// 打开评论模态框
			openComment(){
				// 打开评论模态框
				this.comment_box_modal_show =! this.comment_box_modal_show
			},
			sendMessage(){
				uni.request({
					url:this.$elyasApi+'moment/comment/add',
					method:'POST',
					data:this.comment_Params,
					header:{
						"content-type":"application/json",
						"accessToken":this.$store.state.userInfo.token
					},
					success:(data)=> {
						if (data.data.code !== 200) {
							uni.showToast({
								title:data.data.message
							});
							return;
						}else {
							uni.showToast({
								title:'发表成功！'
							})
							
							this.getArticleDetail(this.articleId)
							
							this.comment_box_modal_show = false
							this.comment_Params.comment = ''
							
						}
					}
					
				});
				
			},
			toTalkPage(name,id){
				uni.navigateTo({
					url:'../chat-window/chat-window?name='+name+'&id='+id
				})
			}
		}
	}
</script>

<style>
	.side-box{
		padding: 30rpx;
	}
	
	.footer-buttons{
		position: fixed;
		bottom: 0rpx;
		left: 0;
		height: 110rpx;
		width: 750rpx;
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 50rpx;
		background-color: #fff;
	}
	
	.comment_box {
		position: fixed;
		bottom: 111rpx;
		left: 0;
		height: 350rpx;
		width: 750rpx;
		/* padding: 50rpx; */
		padding: 10rpx;
		border-top: 1px solid #999;
		background-color: #fff;
	}
</style>
